html,
body {
  margin: auto;
  background: rgb(190, 115, 28);
  background: -webkit-linear-gradient(
    70deg,
    rgb(95, 173, 141) 0%,
    rgba(17, 14, 2, 0.384) 50%,
    rgba(240, 79, 30, 0.39) 100%
  );
  background: -o-linear-gradient(
    70deg,
    rgb(95, 173, 141) 0%,
    rgba(17, 14, 2, 0.384) 50%,
    rgba(240, 79, 30, 0.39) 100%
  );
  background: linear-gradient(
    20deg,
    rgb(95, 173, 141) 0%,
    rgba(17, 14, 2, 0.384) 50%,
    rgba(240, 79, 30, 0.39) 100%
  );
  height: 100%;
}
.small {
  margin-top: 15em;
  display: grid;
  grid-template-columns: repeat(3, 5em);
  grid-template-rows: repeat(3, 2em);
}
#root {
  width: 100%;
  height: 100%;
}
.scoreDisplay {
  margin-top: 83px;
}
.mainDiv {
  height: 100%;
}
.scores {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  overflow-wrap: break-word;
  -o-object-fit: cover;
     object-fit: cover;
  color: white;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100%;
}
h2 {
  margin: 44px;
  font-stretch: none;
  font-size: smaller;
}
h1 {
  margin: auto;
  position: fixed;
  color: rgba(61, 252, 61, 0.705);
  font-stretch: condensed;
  font-family: ChunkFive;
  font-size: inherit;
}
.div {
  font-size: 2.1em;
  text-align: center;
  width: 49px;
  height: 45px;
}
.dis {
  overflow-x: scroll;
  margin-top: 33px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}
.grid {
  margin-top: 83px;
  display: grid;
  overflow-wrap: normal;
  grid-template-columns: repeat(4, 55px);
  grid-template-rows: repeat(20, 55px);
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.walls {
  margin-top: 83px;
  display: grid;
  overflow-wrap: normal;
  grid-template-columns: repeat(20, 2em);
  grid-template-rows: repeat(20, 1.5em);
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.wall {
  border-radius: 4px;
  outline: none;
  text-align: center;
  border: none;
  margin: none;
}
.passage {
  opacity: none;
  width: 104%;
  height: 104%;
  background-color: inherit;
  border-radius: 0.6em;
  text-align: center;
  outline: none;
  background-color: inherit;
  outline-color: white;
}
.barrier {
  background-color: rgb(250, 232, 232);
  outline: none;
  outline-color: none;
  border-radius: 0.3em;
  opacity: 0.5;
}
button {
  color: black;
}
p {
  font-size: xx-small;
  text-align: center;
  overflow: hidden;
  color: rgb(0, 0, 0);
  font-weight: 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.items {
  font-size: 2.1em;
  text-align: center;
  width: 49px;
  height: 45px;
}
.lives {
  font-size: 2.1em;
  text-align: center;
  width: 49px;
  height: 45px;
}
button {
  font-size: 12px;
  font-size-adjust: unset;
  font-synthesis: style;
  text-align: center;
}
button :hover {
  background-color: none;
}
@media screen and (max-width: 80px) {
  .walls {
    width: 50%;
    grid-template-columns: repeat(20, 15px);
    grid-template-rows: repeat(20, 15px);
  }
}

@media screen and (max-width: 100%) {
  .walls {
    width: 50%;
    grid-template-columns: repeat(20, 15px);
    grid-template-rows: repeat(20, 15px);
  }
}
@media screen and (max-width: 902px) {
  .walls {
    width: 100%;
    grid-template-columns: repeat(20, 2em);
    grid-template-rows: repeat(20, 1.5em);
  }
  display: grid;
  font-size: 9%;
  overflow-wrap: normal;
  grid-template-columns: repeat(3, 190px);
  grid-template-rows: repeat(3, 190px);
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}

@media screen and (max-width: 975px) {
  .walls {
    width: 100%;
    grid-template-columns: repeat(20, 2em);
    grid-template-rows: repeat(20, 1.5em);
  }
}



